import React, {Component} from 'react';

const UserTable=(props)=>{
    console.log(props.us)
    return (<table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Apellido</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
            {
                props.us.length > 0?
              props.us.map(us=>(
            <tr key={us.id}>
                <td>{us.nombre}</td>
                <td>{us.username}</td>
                <td>
                  <button className="button muted-button"
                  onClick = {
                    () => {
                      props.editado(us)
                    }
                  }
                  >
                    Edit
                    </button>
                  <button className="button muted-button" onClick={()=>{props.eliminarusuario(us.id)}
                }>

                    eliminar información

                  </button>
                </td>
              </tr>
              )): (
                <tr>
                     <td colSpan={3}>No users</td>
                </tr>
              )})


        </tbody>
      </table>);
}
export default UserTable;