import React, {useState} from 'react';
import UserTable from './componente/UserTable';
import{v4 as uuidv4}from'uuid';
import Agregarusuario from './componente/agregarusuario';
import Editar from './componente/editarusuario';

function App() {

  const infor = [
    {id:uuidv4(), nombre: 'jhon', username: 'floppydiskette' },
    {id: uuidv4(), nombre: 'fer', username: 'siliconeidolon' },
    {id: uuidv4(), nombre: 'mariajose', username: 'benisphere' }
  ]
  //actualizar
  const actualizar = (id,actualizar) => {
    setEdit(false);
    setUs(us.map(usu => (usu.id === id? actualizar : usu)))
  }
    //array
    const[us, setUs] = useState (infor);
// usestate : lleva la informacion para mostrarla
const editado = (usu) => {
  setEdit(true);
  setcurrerentus({
    id: usu.id,nombre:usu.nombre,username:usu.username
  });
}
// crear usuarios
const ingregarusuario=(usu)=>{
  usu.id=uuidv4()
  setUs([
    ...us,
  usu])
}
//eliminar usuario
const eliminarusuario=(id)=>{
  setUs(us.filter(usu=>usu.id !==id))
}
  //editar usuario
  const [edit,setEdit] = useState(false);
  const [currentuser,setcurrerentus]=useState({
    id:null,nombre:'',username:''});


  return (
<div className="container">
      <h1>Proyecto CRUD</h1>
      <div className="flex-row">
        <div className="flex-large">
          {
            edit ? (
              <div>
                <h2>  Editar Informacion</h2>
        <Editar
        currentuser={currentuser}
        actualizar={actualizar}
        />
              </div>
            ) : (
              <div>
              <h2>Ingresar Informacion del Usuario</h2>
          <Agregarusuario ingregarusuario={ingregarusuario}/>
          </div>)
          }

        </div>
        <div className="flex-large">
          <h2>Ver Informacion</h2>
          <UserTable
          us={us}
          eliminarusuario={eliminarusuario}
          editado={editado}
/>
        </div>
      </div>
    </div>
  );
}
export default App;